<template>
  <div>
    <div class="title">
      {{$route.query.k_id!=-1 ? "修改企业动态":"增加企业动态"}}
    </div>
    <div class="WOBox">
        <!-- 选择主题-下拉框、可以选多个 -->
        <div class="info">
            <el-upload
            class="avatar-uploader"
            action=""
            :show-file-list="false"
            :before-upload="beforeupload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon">
                封面图
            </i>
            </el-upload>
        </div> 
        <!-- 内容编辑区域 -->
      <div class="textBox">
        标题：<el-input v-model="input" class="inp" placeholder="请输入内容" maxlength="32"></el-input>
        <!-- 富文本插件start -->
        <div ref="editor" style="text-align:left;position: relative;z-index: 9;"></div>
        <!-- 富文本插件end -->
      </div>
      <el-button type="primary" @click="getContent">{{$route.query.k_id!=-1 ? "修改":"保存"}}</el-button>
    </div>
  </div>
</template>

<script>
  import E from 'wangeditor'
  import url from "../../common/api";
  import { mapState } from 'vuex'
  export default {
    data() {
      return {
        editorContent:'',//这是富文本的内容
        editor:null,//这是富文本继承的方法挂载变量
        input:'',//这是标题
        imageUrl:'',//封面图
        param:new FormData(),//表单要提交的参数
      };
    },
    mounted() {
      this.editor = new E(this.$refs.editor)
      this.editor.customConfig.onchange = (html,a) => {
        this.editorContent = html
      }
      this.editor.customConfig.uploadImgServer =this.uploadimage   // 上传图片到服务器
      this.editor.customConfig.uploadFileName = 'image'
            this.editor.customConfig.uploadImgHooks = {  //监听
                customInsert: function (insertImg, result, editor) {
                    var url=result.ip + result.data[0]
                    insertImg(url)
                }
            }
      this.editor.customConfig.showLinkImg = false
      this.editor.create()
      if(this.$route.query.k_id!=-1){
        this.Fneditknowledge()
      }
    },
    methods: {
      getContent(){//点击保存按钮触发的事件
        this.param.append('e_title', this.input);
        this.param.append('user_id', this.buserInfo.user_id);
        this.param.append('e_content', this.editorContent);
        if(this.$route.query.k_id==-1){
          // 保存
          if(this.input!='' && this.editorContent!='' && this.imageUrl!=''){
            console.log(this.imageUrl,'-----------这是看见')
            url.addenterprise(this.param).then(res=>{
              console.log(res,this.imageUrl,'----------添加企业资讯')
              if(res.data.code[0]=='200'){
                this.$message.success(res.data.message[0]);
                this.$router.push('/bkIndex/dynamicB')
              }
            })          
          }else{
            this.$message.error('封面图、主题以及问题内容不能为空');
          }
        }else{
            this.param.append('k_id', this.$route.query.k_id);
          if(this.input!='' && this.editorContent!='' && this.imageUrl!=''){
            // 修改请求
            url.Pviewenterprise(this.param).then(res=>{
              console.log(res,'===========修改')
              if(res.data.code[0]=='200'){
                this.$message.success(res.data.message[0]);
                this.$router.push('/bkIndex/dynamicB')
              }
            })  
          }else{
            this.$message.error('标题、主题以及问题内容不能为空');
          }
        }
        console.log(this.editorContent,'----------------------')
      },
      Fneditknowledge(){//查看单个的知识库详情
          url.viewenterprise({
              k_id:this.$route.query.k_id
          })
          .then(res=>{
              console.log(res,'---------查看详情')
              if(res.data.code[0]='200'){
                  let xx=res.data.data[0].fields
                  this.editor.txt.html(xx.e_content)
                  this.input=xx.e_title
                  this.imageUrl=this.static+xx.e_image
              }
          })
      },
      beforeupload(file) {
          console.log(file);
          //创建临时的路径来展示图片
          var windowURL = window.URL || window.webkitURL;
          this.imageUrl=windowURL.createObjectURL(file);
          this.param.append('e_image', file);
          return false;
      },
    },
    computed:{
      ...mapState(['buserInfo','uploadimage','tagList','static'])
    }
  };
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.WOBox
    padding 30px
    .info
      margin-bottom 20px
    .textBox
        width 950px
        height 667px
        margin-bottom 20px
        .inp
          font-size 24px
          margin-bottom 15px
          >>> .el-input__inner
            border none
.textBox >>> .w-e-text-container
  min-height 580px !important
>>>.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
>>>.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
>>>.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width 350px
    height 200px
    line-height: 200px;
    text-align: center;
}
>>>.avatar {
    width 350px
    height 200px
    display: block;
}
</style>





